.main {
  .container-hero-main {
    height: calc(100vh - 5.6rem);
    border-left: 0.1rem solid $color-line;
    border-right: 0.1rem solid $color-line;
    border-bottom: 0.1rem solid $color-line;
    border-radius: 0.8rem;
    @include tablet {
      height: auto;
      height: calc(100vh - 12.5rem);
    }
    @include desktop {
      height: calc(100vh - 12.5rem);
    }
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .container-project-main {
    height: auto;
    border-left: 0.1rem solid $color-line;
    border-right: 0.1rem solid $color-line;
    border-bottom: 0.1rem solid $color-line;
    border-radius: 0.8rem;
    @include tablet {
      height: auto;
      border-bottom: none;
    }
    @include desktop {
      height: calc(100vh - 12.5rem);
    }
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
