html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

::-webkit-scrollbar {
  display: none;
}
body {
  animation: fade-in 1s ease-in-out;
  font-family: $font-principal;
  font-size: 1.6rem;
  line-height: 1.8;
  height: 100vh;

  padding: 0.5rem;
  background-color: $background;
  background-image: url(/build/img/Background-Blurs.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  @include desktop {
    background-size: contain;
    background-position-x: 115%;
    background-position-y: 50%;
  }
  @include tablet {
    padding: 1rem;
  }
}

.loaded {
  opacity: 1;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

p {
  color: $blue;
  font-size: 2rem;
  margin: 0;
}

a {
  text-decoration: none;
  color: $blue;
}

img,
picture {
  width: 100%;
  display: block;
}

h1,
h2,
h3 {
  font-family: $font-principal;
  margin: 0;
  font-weight: 300;
  text-align: center;
  color: $white;
}

h1 {
  font-size: 6.2rem;
}
h2 {
  font-size: 3.4rem;
}
h3 {
  font-size: 3rem;
}
h4 {
  font-size: 2.6rem;
}

.line {
  height: 0.1rem;
  background: $color-line;
  max-width: 100%;
}
