.container-header {
  border: 0.1rem solid $color-line;
  border-radius: 0.8rem 0.8rem 0 0;
}
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 5.6rem;
  position: relative;

  .header-logo {
    padding-left: 1.8rem;
    height: 100%;
    display: flex;
    align-items: center;
    @include tablet {
      flex: 0.4;
      border-right: 0.1rem solid $color-line;
    }
  }

  .header-nav {
    z-index: 500;
    position: absolute;
    top: 5.6rem;
    right: -200%;
    background: $background;

    border: 0.1rem solid $color-line;
    width: 100%;
    height: calc(100vh - 56px);

    display: flex;
    flex-direction: column;
    align-items: center;
    @include tablet {
      flex: 2;
      position: relative;
      top: auto;
      right: auto;
      width: 100%;
      border: none;

      height: auto;
      flex-direction: row;
      justify-content: space-between;
    }
    .container-link {
      height: 5.5rem;
      padding: 1.7rem 1.8rem;
      width: 100%;
      border-bottom: 0.1rem solid $color-line;
      position: relative;

      .line {
        position: absolute;
        left: 0;
        bottom: 0;
        width: 0;
        height: 0.3rem;
        background: $orange;
        transition: 0.5s width ease;
      }

      &:hover {
        .line {
          width: 100%;
        }
      }
      @include tablet {
        width: auto;
        border: none;
        padding: 0 3.2rem;
        display: flex;
        align-items: center;
      }
    }
    .active {
      .line {
        width: 100%;
      }
      a{
        color: $white;
      }
    }

    .nav-2 {
      display: flex;
      flex-direction: column;
      width: 100%;
      @include tablet {
        flex-direction: row;
      }
      .container-link {
        @include tablet {
          border-right: 0.1rem solid $color-line;
        }
      }
    }
    .item-contacto {
      @include tablet {
        border-left: 0.1rem solid $color-line;
      }
    }
  }
  .nostrar {
    right: 0;
  }

  .btn-bar {
    display: flex;
    background: transparent;
    border: none;
    padding: 0 1.8rem 0 0;
    @include tablet {
      display: none;
    }
  }
}
