.container-footer {
  border: 0.1rem solid $color-line;
  border-radius: 0 0 0.8rem 0.8rem;
  height: 5rem;
  display: none;
  @include tablet {
    display: block;
  }
}
.mostrar-footer {
  display: block;
}
.footer {
  display: flex;
  justify-content: space-between;
  height: 100%;
  padding-left: 1.8rem;

  .footer-1 {
    display: flex;
    align-items: center;
    .container-link {
      display: flex;
      align-items: center;
      height: 100%;
      padding: 1.5rem 2.2rem 1.5rem 0;
      border-right: 0.1rem solid $color-line;

      &:nth-child(2) {
        padding: 0;
        padding: 0 1.4rem;
        transition: 0.5s background-color ease;
        &:hover {
          background-color: $morado;
          svg path {
            fill: $white;
          }
        }
      }
    }
  }
  .footer-2 {
    border-left: 0.1rem solid $color-line;
    display: flex;
    align-items: center;

    a {
      padding: 0 2.2rem;
      width: 100%;
      height: 100%;
      transition: 0.5s background-color ease;
      &:hover {
        background-color: $morado;
        p{
          color: $white;
        }
        svg path {
          fill: $white;
        }
      }
      p {
        font-size: 1.6rem;
        font-style: normal;
        font-weight: 450;
        line-height: normal;
        display: none;
        @include tablet {
          display: block;
        }
      }
      display: flex;
      align-items: center;
      gap: 1.5rem;
    }
  }
}
