.proyecto {
  margin: 1.8rem 0;
  @include tablet{
    margin: 2rem 0;
  }
}

.container-projects {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  column-gap: 4rem;
  row-gap: 2rem;
  @include tablet {
    grid-template-columns: repeat(2, 1fr);
    row-gap: 2.5rem;
  }
  @include desktop {
    grid-template-columns: repeat(3, 1fr);
  }
}

.cotainer-card {
  .title-project {
    margin-bottom: 1.5rem;
    color: $blue-2;
    font-size: 1.6rem;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    span {
      color: $blue;
    }
  }

  .card {
    background: $background-card;
    border-radius: 1.5rem;
    border: 1px solid #1e2d3d;
    .img-card {
      img {
        border-radius: 1.5rem 1.5rem 0 0;
      }
    }
    .info-card {
      padding: 2.4rem 3.1rem 3.1rem 3.1rem;
      .description {
        color: $blue;
        font-size: 1.6rem;
        font-style: normal;
        font-weight: 450;
        line-height: 150%;
        margin-bottom: 2.188rem;
        @include tablet {
          font-size: 1.8rem;
        }
      }
      .btn-container {
        .btn {
          position: relative;
          padding: 1rem 1.4rem;
          background: $color-btn;
          border: 1px solid $color-btn;
          border-radius: 0.8rem;
          width: fit-content;
          transition: 0.5s border-color ease;
          color: $white;
          font-size: 1.4rem;
          font-style: normal;
          font-weight: 450;
          line-height: normal;
          &:hover {
            border-color: $green;
          }
        }
      }
    }
  }
}
