body {
  animation: fade-in 1s ease-in-out;
  font-family: "Fira Code", monospace;
  font-size: 1.6rem;
  line-height: 1.8;
  height: 100vh;
  padding: 0.5rem;
  background-color: #011627;
  background-image: url(/build/img/Background-Blurs.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
@media (min-width: 1024px) {
  body {
    background-size: contain;
    background-position-x: 115%;
    background-position-y: 50%;
  }
}
@media (min-width: 768px) {
  body {
    padding: 1rem;
  }
}

.loaded {
  opacity: 1;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

p {
  color: #607B96;
  font-size: 2rem;
  margin: 0;
}

a {
  text-decoration: none;
  color: #607B96;
}

img,
picture {
  width: 100%;
  display: block;
}

h1,
h2,
h3 {
  font-family: "Fira Code", monospace;
  margin: 0;
  font-weight: 300;
  text-align: center;
  color: #FFFFFF;
}

h1 {
  font-size: 6.2rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.line {
  height: 0.1rem;
  background: #1E2D3D;
  max-width: 100%;
}

.container-header {
  border: 0.1rem solid #1E2D3D;
  border-radius: 0.8rem 0.8rem 0 0;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 5.6rem;
  position: relative;
}
.header .header-logo {
  padding-left: 1.8rem;
  height: 100%;
  display: flex;
  align-items: center;
}
@media (min-width: 768px) {
  .header .header-logo {
    flex: 0.4;
    border-right: 0.1rem solid #1E2D3D;
  }
}
.header .header-nav {
  z-index: 500;
  position: absolute;
  top: 5.6rem;
  right: -200%;
  background: #011627;
  border: 0.1rem solid #1E2D3D;
  width: 100%;
  height: calc(100vh - 56px);
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .header .header-nav {
    flex: 2;
    position: relative;
    top: auto;
    right: auto;
    width: 100%;
    border: none;
    height: auto;
    flex-direction: row;
    justify-content: space-between;
  }
}
.header .header-nav .container-link {
  height: 5.5rem;
  padding: 1.7rem 1.8rem;
  width: 100%;
  border-bottom: 0.1rem solid #1E2D3D;
  position: relative;
}
.header .header-nav .container-link .line {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 0;
  height: 0.3rem;
  background: #FEA55F;
  transition: 0.5s width ease;
}
.header .header-nav .container-link:hover .line {
  width: 100%;
}
@media (min-width: 768px) {
  .header .header-nav .container-link {
    width: auto;
    border: none;
    padding: 0 3.2rem;
    display: flex;
    align-items: center;
  }
}
.header .header-nav .active .line {
  width: 100%;
}
.header .header-nav .active a {
  color: #FFFFFF;
}
.header .header-nav .nav-2 {
  display: flex;
  flex-direction: column;
  width: 100%;
}
@media (min-width: 768px) {
  .header .header-nav .nav-2 {
    flex-direction: row;
  }
}
@media (min-width: 768px) {
  .header .header-nav .nav-2 .container-link {
    border-right: 0.1rem solid #1E2D3D;
  }
}
@media (min-width: 768px) {
  .header .header-nav .item-contacto {
    border-left: 0.1rem solid #1E2D3D;
  }
}
.header .nostrar {
  right: 0;
}
.header .btn-bar {
  display: flex;
  background: transparent;
  border: none;
  padding: 0 1.8rem 0 0;
}
@media (min-width: 768px) {
  .header .btn-bar {
    display: none;
  }
}

.main .container-hero-main {
  height: calc(100vh - 5.6rem);
  border-left: 0.1rem solid #1E2D3D;
  border-right: 0.1rem solid #1E2D3D;
  border-bottom: 0.1rem solid #1E2D3D;
  border-radius: 0.8rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (min-width: 768px) {
  .main .container-hero-main {
    height: auto;
    height: calc(100vh - 12.5rem);
  }
}
@media (min-width: 1024px) {
  .main .container-hero-main {
    height: calc(100vh - 12.5rem);
  }
}
.main .container-project-main {
  height: auto;
  border-left: 0.1rem solid #1E2D3D;
  border-right: 0.1rem solid #1E2D3D;
  border-bottom: 0.1rem solid #1E2D3D;
  border-radius: 0.8rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (min-width: 768px) {
  .main .container-project-main {
    height: auto;
    border-bottom: none;
  }
}
@media (min-width: 1024px) {
  .main .container-project-main {
    height: calc(100vh - 12.5rem);
  }
}

.container-footer {
  border: 0.1rem solid #1E2D3D;
  border-radius: 0 0 0.8rem 0.8rem;
  height: 5rem;
  display: none;
}
@media (min-width: 768px) {
  .container-footer {
    display: block;
  }
}

.mostrar-footer {
  display: block;
}

.footer {
  display: flex;
  justify-content: space-between;
  height: 100%;
  padding-left: 1.8rem;
}
.footer .footer-1 {
  display: flex;
  align-items: center;
}
.footer .footer-1 .container-link {
  display: flex;
  align-items: center;
  height: 100%;
  padding: 1.5rem 2.2rem 1.5rem 0;
  border-right: 0.1rem solid #1E2D3D;
}
.footer .footer-1 .container-link:nth-child(2) {
  padding: 0;
  padding: 0 1.4rem;
  transition: 0.5s background-color ease;
}
.footer .footer-1 .container-link:nth-child(2):hover {
  background-color: #4D5BCE;
}
.footer .footer-1 .container-link:nth-child(2):hover svg path {
  fill: #FFFFFF;
}
.footer .footer-2 {
  border-left: 0.1rem solid #1E2D3D;
  display: flex;
  align-items: center;
}
.footer .footer-2 a {
  padding: 0 2.2rem;
  width: 100%;
  height: 100%;
  transition: 0.5s background-color ease;
  display: flex;
  align-items: center;
  gap: 1.5rem;
}
.footer .footer-2 a:hover {
  background-color: #4D5BCE;
}
.footer .footer-2 a:hover p {
  color: #FFFFFF;
}
.footer .footer-2 a:hover svg path {
  fill: #FFFFFF;
}
.footer .footer-2 a p {
  font-size: 1.6rem;
  font-style: normal;
  font-weight: 450;
  line-height: normal;
  display: none;
}
@media (min-width: 768px) {
  .footer .footer-2 a p {
    display: block;
  }
}

.hero-container {
  width: 95%;
  max-width: 1000px;
  margin: 0 auto;
  padding: 0 0 0 2.7rem;
  height: auto;
}
@media (min-width: 1024px) {
  .hero-container {
    padding: 0 27rem;
    width: auto;
    max-width: 100%;
    margin: auto;
  }
}

.hero {
  display: flex;
  flex-direction: column;
  position: relative;
}
@media (min-width: 768px) {
  .hero {
    flex-direction: row;
    gap: 5.5rem;
    align-items: center;
  }
}

.hero-info {
  flex: auto;
}
@media (min-width: 768px) {
  .hero-info {
    flex: 1.5;
    z-index: 1;
  }
}
.hero-info .an-title {
  color: #E5E9F0;
  font-size: 1.8rem;
  font-weight: 450;
  margin-bottom: 1.9rem;
}
@media (min-width: 768px) {
  .hero-info .an-title {
    margin: 0;
  }
}
.hero-info .title {
  text-align: left;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  color: #E5E9F0;
  font-size: 5rem;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .hero-info .title {
    font-size: 6.2rem;
    margin: 0;
  }
}
.hero-info .sub-title {
  text-align: left;
  color: #4D5BCE;
  font-size: 2rem;
  font-style: normal;
  font-weight: 450;
  line-height: normal;
  margin-bottom: 3.1rem;
}
@media (min-width: 768px) {
  .hero-info .sub-title {
    font-size: 3.2rem;
    margin-bottom: 2.9rem;
  }
}
.hero-info .paragraf {
  width: 267px;
  font-size: 1.6rem;
  font-style: normal;
  font-weight: 450;
  line-height: normal;
  margin-bottom: 3.2rem;
}
@media (min-width: 768px) {
  .hero-info .paragraf {
    margin-bottom: 3rem;
    width: 489px;
  }
}
.hero-info .cv {
  font-size: 1.4rem;
  font-style: normal;
  font-weight: 500;
  line-height: 140%;
  color: #43D9AD;
}
@media (min-width: 768px) {
  .hero-info .cv {
    line-height: normal;
    font-size: 1.6rem;
  }
}
.hero-info .cv span {
  color: #4D5BCE;
}
.hero-info .cv a {
  color: #E99287;
}

.container-img {
  display: none;
}
@media (min-width: 768px) {
  .container-img {
    display: block;
    flex: 1;
  }
}

.hero-img {
  width: 510px;
  height: 475px;
  position: absolute;
  flex-shrink: 0;
  border-radius: 8px;
  position: relative;
}
@media (min-width: 768px) {
  .hero-img {
    z-index: 0;
    width: 350px;
    height: 385px;
    top: 21rem;
    right: 15rem;
    background: none;
    border: none;
    box-shadow: none;
  }
}
@media (min-width: 1024px) {
  .hero-img {
    width: 510px;
    height: 475px;
    top: auto;
    right: auto;
    border: 1px solid #0c1616;
    background: linear-gradient(150deg, rgba(23, 85, 83, 0.7) 1.7%, rgba(67, 217, 173, 0.09) 81.82%);
    box-shadow: 0px 2px 0px 0px rgba(255, 255, 255, 0.3) inset;
    backdrop-filter: blur(32px);
  }
}
.hero-img img {
  position: absolute;
  bottom: 0;
  border-radius: 0 0 0.8rem 0.8rem;
}

.proyecto {
  margin: 1.8rem 0;
}
@media (min-width: 768px) {
  .proyecto {
    margin: 2rem 0;
  }
}

.container-projects {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  column-gap: 4rem;
  row-gap: 2rem;
}
@media (min-width: 768px) {
  .container-projects {
    grid-template-columns: repeat(2, 1fr);
    row-gap: 2.5rem;
  }
}
@media (min-width: 1024px) {
  .container-projects {
    grid-template-columns: repeat(3, 1fr);
  }
}

.cotainer-card .title-project {
  margin-bottom: 1.5rem;
  color: #5565E8;
  font-size: 1.6rem;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}
.cotainer-card .title-project span {
  color: #607B96;
}
.cotainer-card .card {
  background: #011221;
  border-radius: 1.5rem;
  border: 1px solid #1e2d3d;
}
.cotainer-card .card .img-card img {
  border-radius: 1.5rem 1.5rem 0 0;
}
.cotainer-card .card .info-card {
  padding: 2.4rem 3.1rem 3.1rem 3.1rem;
}
.cotainer-card .card .info-card .description {
  color: #607B96;
  font-size: 1.6rem;
  font-style: normal;
  font-weight: 450;
  line-height: 150%;
  margin-bottom: 2.188rem;
}
@media (min-width: 768px) {
  .cotainer-card .card .info-card .description {
    font-size: 1.8rem;
  }
}
.cotainer-card .card .info-card .btn-container .btn {
  position: relative;
  padding: 1rem 1.4rem;
  background: #1C2B3A;
  border: 1px solid #1C2B3A;
  border-radius: 0.8rem;
  width: fit-content;
  transition: 0.5s border-color ease;
  color: #FFFFFF;
  font-size: 1.4rem;
  font-style: normal;
  font-weight: 450;
  line-height: normal;
}
.cotainer-card .card .info-card .btn-container .btn:hover {
  border-color: #43D9AD;
}