.hero-container {
  width: 95%;
  max-width: 1000px;
  margin: 0 auto;
  padding: 0 0 0 2.7rem;
  height: auto;
  @include desktop {
    padding: 0 27rem;
    width: auto;
    max-width: 100%;
    margin: auto;
  }
}
.hero {
  display: flex;
  flex-direction: column;
  position: relative;

  @include tablet {
    flex-direction: row;
    gap: 5.5rem;
    align-items: center;
  }
}

.hero-info {
  flex: auto;
  @include tablet {
    flex: 1.5;
    z-index: 1;
  }

  .an-title {
    color: $white-2;
    font-size: 1.8rem;
    font-weight: 450;
    margin-bottom: 1.9rem;
    @include tablet {
      margin: 0;
    }
  }
  .title {
    text-align: left;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    color: $white-2;
    font-size: 5rem;
    margin-bottom: 2rem;
    @include tablet {
      font-size: 6.2rem;
      margin: 0;
    }
  }
  .sub-title {
    text-align: left;
    color: $morado;
    font-size: 2rem;
    font-style: normal;
    font-weight: 450;
    line-height: normal;
    margin-bottom: 3.1rem;
    @include tablet {
      font-size: 3.2rem;
      margin-bottom: 2.9rem;
    }
  }
  .paragraf {
    width: 267px;
    font-size: 1.6rem;
    font-style: normal;
    font-weight: 450;
    line-height: normal;
    margin-bottom: 3.2rem;
    @include tablet {
      margin-bottom: 3rem;
      width: 489px;
    }
  }
  .cv {
    font-size: 1.4rem;
    font-style: normal;
    font-weight: 500;
    line-height: 140%;
    color: $green;
    @include tablet {
      line-height: normal;
      font-size: 1.6rem;
    }

    span {
      color: $morado;
    }
    a {
      color: $red;
    }
  }
}

.container-img {
  display: none;
  @include tablet {
    display: block;
    flex: 1;
  }
}

.hero-img {
  width: 510px;
  height: 475px;
  position: absolute;
  flex-shrink: 0;
  border-radius: 8px;

  @include tablet {
    z-index: 0;
    width: 350px;
    height: 385px;
    top: 21rem;
    right: 15rem;
    background: none;
    border: none;
    box-shadow: none;
  }
  @include desktop {
    width: 510px;
    height: 475px;
    top: auto;
    right: auto;

    border: 1px solid #0c1616;

    background: linear-gradient(
      150deg,
      rgba(23, 85, 83, 0.7) 1.7%,
      rgba(67, 217, 173, 0.09) 81.82%
    );

    box-shadow: 0px 2px 0px 0px rgba(255, 255, 255, 0.3) inset;
    backdrop-filter: blur(32px);
  }
  position: relative;
  img {
    position: absolute;
    bottom: 0;
    border-radius: 0 0 0.8rem 0.8rem;
  }
}
